:root{
  --primary-color: #337033;
  --secondary-color: #578c57;
  --white: #FFFFFF;
  --padding-container: 8px;
  --border-radius: 12px;
  --margin-top-bottom: 5vh;
}
@media (min-width: 1024px) {
  :root{
    --padding-container: 8rem;
  }
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0;
  padding: 0;
  color: #000000;
}

Link, a{
  text-decoration: none;
}

button{
  border: none;
  outline: none;
  color: inherit;
}